.navigator-tabs {
  margin-bottom: 15px;
  .btn {
    display: block;
    span, small {
      display: block;
      text-align: center;
    }
    small {
      font-size: 12px;
      font-weight: 100;
    }
  }
}

.navigator-tabs__name {
  background-color: #40b3b7;
  padding: 15px;
  padding-left: 20px;
  border-bottom: 1px solid #fff;
  min-height: 70px;
  display: flex;
  align-items: center;
  h2 {
    font-size: 18px;
    color: #fff;
    margin: 0;
  }
}

.navigator-tabs__item {

}

.navigator-tabs__head {
  padding: 15px;
  padding-left: 20px;
  padding-right: 40px;
  background-color: #e2e2e2;
  border-bottom: 1px solid #fff;
  position: relative;
  min-height: 70px;
  display: flex;
  align-items: center;
  &.active {
    &::before {
      transform: translateY(-50%) rotateZ(180deg);
    }
  }
  &::before {
    content: '';
    display: block;
    width: 14px;
    height: 6px;
    background-image: url(../media/images/svg/down-arr.svg);
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    transition: transform 300ms ease;
  }
  h3 {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
  }
}

.navigator-tabs__content {
  padding: 15px;
  background-color: #f4f4f4;
  display: none;
  font-size: 14px;
  h4 {
    margin-top: 0;
    margin-bottom: 8px;
    font-size: 16px;
  }
  p {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  ul {
    padding-left: 15px;
    margin-top: 8px;
    margin-bottom: 8px;
  }
}


.navigator-inner-tab {
  margin-left: -15px;
  margin-right: -15px;
  background-color: #fafafb;
  border: 1px solid #e6e6e6;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
  
}
.navigator-inner-tab__head {
  padding-left: 15px;
  padding-right: 30px;
  padding-top: 12px;
  padding-bottom: 12px;
  position: relative;
  &::after {
    content: '';
    transition: transform ease 300ms;
    position: absolute;
    width: 0;
    height: 0;
    right: 20px;
    top: 50%;
    margin-top: -2px;
    border-top: 4px solid #444;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
  }
  &.active {
    background-color: #e2e2e2;
    &::after {
      transform: rotate(180deg);
    }
  }
  h5 {
    margin: 0;
    font-size: 16px;
  }
}
.navigator-inner-tab__content {
  padding: 15px;
  display: none;
}