.abc-nav {
  display: flex;
  overflow-x: scroll;
  background-color: #40b3b7;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  padding: 15px;
  padding-left: 17px;
  font-size: 18px;
  margin-bottom: 15px;
  margin-top: 15px;

  a {
    color: #fff;
  }
}

.abc-nav__cont {
  position: relative;
  &:before {
    content: '';
    position: absolute;
    display: block;
    height: 100%;
    width: 17px;
    top: 0;
    left: calc(100% - 17px);
    background: linear-gradient(90deg, rgba(64, 179, 183, 0) 0%, rgba(64, 179, 183, 0.7) 51.13%, #40B3B7 100%);
  }
}

.abc-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-left: 15px;
  padding-right: 15px;
  li {
    padding-left: 8px;
    padding-right: 8px;
  }
}