.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.hidden {
  display: none;
}

.hide-scroll {
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }

  &::-webkit-scrollbar-thumb {
    height: 0;
    width: 0;
  }

  &::-webkit-scrollbar-track {
    height: 0;
    width: 0;
  }
}

.hide-overflow {
  overflow: hidden;
}


.content-right {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.content-center {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.sticky {
  position: sticky !important;
  top: 0;
  z-index: 10;
  will-change: top;
  @media (max-width: 760px) {
    padding-top: 0 !important;
  }
}



////////////////////////
.highlight-animate {
  animation: highlight 1500ms ease 300ms 2 normal backwards;
}

@keyframes highlight {
  0% {
    border-color: #e5e5e5
  }

  25%,75% {
      border-color: #40b3b7;
      box-shadow: 0 0 10px #40b3b7
  }

  50%,to {
      border-color: #e5e5e5;
      box-shadow: 0 0 0 transparent
  }
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}