.pagination {

}

.pagination__next {
  margin-bottom: 10px;
  a {
    font-size: 20px;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  span {
    font-size: 20px;
  }
}

.pagination__pages {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    display: inline-block;

    a {
      padding-left: 13px;
      padding-right: 13px;
      font-size: 20px;
      text-decoration: underline;
      display: inline-block;
      background-color: #fff;
      transition: all ease 200ms;
      &:hover {
        background-color: #40b3b7;
        color: #fff;
      }
    }

    .pagination__current-page {
      background-color: #40b3b7;
      color: #fff;
    }
  }
}