.diseases-links {
  columns: 3;
  column-gap: 20px;
  padding-bottom: 60px;
  @media (max-width: 1080px) {
    columns: 2;
  }
  @media (max-width: 760px) {
    columns: 1;
    margin-top: -15px;
  }
}
.diseases-links__item-cont {
  padding-bottom: 10px;
  padding-top: 10px;
}
.diseases-links__item {
  padding: 20px;
  padding-top: 42px;
  break-inside: avoid-column;
  position: relative;
  border: solid 1px #e5e5e5;
  transition: border-color 300ms ease;

  &:hover {
    border-color: #8cd1d4;
    h3, &::before {
      color: #8cd1d4;
    }
  }
  &::before {
    transition: color 300ms ease;
    content: attr(data-letter);
    position: absolute;
    top: 20px;
    left: 24px;
    font-size: 40px;
    line-height: 1;
    text-transform: uppercase;
    font-weight: 700;
    color: #d6d6d6;
    @media (max-width: 760px) {
      font-size: 30px;
      top: 16px;
      left: 16px;
    }
  }
  h3 {
    transition: color 300ms ease;
    color: #d6d6d6;
    font-size: 30px;
    font-weight: 700;
    margin: 0;
    margin-bottom: 20px;
    margin-top: -20px;
  }
}

.diseases-links__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding-left: 65px;
  @media (max-width: 760px) {
    padding-left: 30px;
  }

  &_padding-off {
    padding: 0;
  }

  li {
    &:not(:last-of-type) {
      margin-bottom: 12px;
    }
    a {
      font-size: 18px;
      @media (max-width: 760px) {
        font-size: 14px;
      }
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
}