.lib-advices {
  padding-bottom: 45px;
}

.lib-advices__sidebar {
  background-color: #40b3b7;
  padding: 20px;
}
.lib-advices__sidebar-links-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    padding-top: 12px;
    padding-bottom: 12px;
    &:not(:first-of-type) {
      border-top: 1px solid #55c6c9;
    } 
    a {
      color: #fff;
      font-weight: 700;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.lib-advices__content {
  h1 {
    margin-top: 0;
  }
}


.lib-advices__items-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.lib-advices__item  {
  display: flex;
  align-items: flex-start;
  border: 1px solid #ddd;
  margin-bottom: 30px;
  padding: 20px;
  transition: border-color 300ms ease;
  @media (max-width: 760px) {
    flex-wrap: wrap;
    padding: 15px;
    padding-bottom: 20px;
  }
  &:hover {
    border-color: #8cd1d4;
  }
}
.lib-advices__item-img {
  margin-right: 20px;
  flex-grow: 0;
  flex-shrink: 0;
  @media (max-width: 760px) {
    width: 100%;
    margin: 0;
    margin-bottom: 15px;
  }
  img {
    display: block;
    @media (max-width: 760px) {
      width: 100%;
    }
  }
}

.lib-advices__item-text {
  flex-grow: 1;
  flex-shrink: 1;
  h2 {
    margin-top: 0;
    font-size: 20px;
    a {
      border-bottom: 2px solid #d4d4d4;
      &:hover {
        border-bottom: 2px solid transparent;
      }
    }
  }
  p {
    margin-bottom: 0;
    @media (max-width: 760px) {
      font-size: 14px;
    }
  }
}