body {
  color: #333;
  line-height: 1.5;
  font-family: GothamPro, sans-serif;
  * {
    box-sizing: border-box;
  }
}

h1 {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 24px;
  @media (max-width: 760px) {
    font-size: 20px;
  }
}

h1, h2, h3 {
  line-height: 1.2;
}

a {
  color: #000;
  text-decoration: none;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  width: 100%;
  max-width: 1240px;
  margin: auto;
  &_narrow {
    max-width: 980px;
    padding-left: 10px;
    padding-right: 10px;
  }

  @media (min-width: 760px) {
    padding-top: 0;
  }
}

