.text-article {
  padding-bottom: 16px;
  &_description {
    img:first-of-type {
      float: left;
      margin: 16px 20px 16px 0px;
    }
  }

  img {
    margin: 10px;
    width: 100%;
    @media(min-width: 760px) {
      width: auto;
    }
  }

  h2 {
    border-bottom: 1px solid #bcbcbc;
    padding-bottom: 4px;
  }

  a {
    color: #1bb2c0;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }

  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

.text-article__list {
  padding-left: 20px;
  li {
    margin-bottom: 4px;
  }
}