.dropdown-header {
  padding: 15px;
  border: 2px solid #40b3b7;
  margin-bottom: 5px;
  background-color: #fff;
}
.dropdown-header__head {
  position: relative;
  padding-right: 45px;
  
  &_colored-head {
    color: #40b3b7;
  }
  h1, h2, h3 {
    font-size: 20px;
    margin: 0;
  }
  &:before {
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-image: url(../media/images/svg/list-items.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
  }
}

.dropdown-header__links {
  display: none;
  ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    margin-top: 15px;
    li {
      width: 100%;
      font-size: 14px;
      margin-top: 5px;
      margin-bottom: 5px;
      a {
        text-decoration: underline;
      }
    }
  }
}