body {
  color: #333;
  line-height: 1.5;
  font-family: GothamPro, sans-serif;
}
body * {
  box-sizing: border-box;
}
h1 {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 24px;
}
@media (max-width: 760px) {
  h1 {
    font-size: 20px;
  }
}
h1,
h2,
h3 {
  line-height: 1.2;
}
a {
  color: #000;
  text-decoration: none;
}
.container {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  width: 100%;
  max-width: 1240px;
  margin: auto;
}
.container_narrow {
  max-width: 980px;
  padding-left: 10px;
  padding-right: 10px;
}
@media (min-width: 760px) {
  .container {
    padding-top: 0;
  }
}
@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/gothamPro/GothamPro-Medium.eot");
  src: local("../fonts/gothamPro/Gotham Pro Medium"), local("GothamPro-Medium"), url("../fonts/gothamPro/GothamPro-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/gothamPro/GothamPro-Medium.woff") format("woff"), url("../fonts/gothamPro/GothamPro-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/gothamPro/GothamPro.eot");
  src: local("../fonts/gothamPro/Gotham Pro"), local("GothamPro"), url("../fonts/gothamPro/GothamPro.eot?#iefix") format("embedded-opentype"), url("../fonts/gothamPro/GothamPro.woff") format("woff"), url("../fonts/gothamPro/GothamPro.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.fade-scale-enter-active,
.fade-scale-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}
.fade-scale-enter,
.fade-scale-leave-to {
  opacity: 0;
  transform: scale(0.9);
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.header {
  padding: 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
}
.header__logo-link {
  width: 161px;
  height: 39px;
  background-image: url("../media/images/svg/napopravku-logo.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  margin-left: 20px;
  display: inline-block;
}
.header__nfo-links {
  background-image: url("../static/media/images/svg/medkit-mini.svg");
}
.nav-menu {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  transform: translate3d(-100%, 0, 0);
  transition: all ease 300ms;
  line-height: 1.2;
  z-index: 100;
}
.nav-menu.is-active {
  transform: translate3d(0, 0, 0);
}
.nav-menu__container {
  height: 100%;
  display: inline-block;
  background-color: #fff;
  padding-bottom: 50px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.nav-menu__auth {
  background-color: #c5e8e9;
  padding: 14px 20px;
  border-bottom: 1px solid #ffffff;
}
.nav-menu__auth a {
  display: inline-block;
  padding-left: 24px;
  padding-right: 20px;
  padding-top: 4px;
  padding-bottom: 4px;
  background-repeat: no-repeat;
  background-position: left 2px;
}
.nav-menu__auth a:first-of-type {
  background-image: url("../media/images/svg/lock.svg");
}
.nav-menu__auth a:last-of-type {
  border-left: 1px solid #adc9ca;
}
.nav-menu__city-select {
  padding: 16px 20px;
  background-color: #c5e8e9;
  position: relative;
}
.nav-menu__city-select::before {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  right: 31px;
  top: 24px;
  border-top: 4px solid #809697;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
.nav-menu__city-select select {
  background-color: transparent;
  border: none;
  padding-left: 20px;
  background-image: url("../media/images/svg/marker.svg");
  background-position: left top;
  background-repeat: no-repeat;
  display: block;
  width: 100%;
  appearance: none;
  line-height: 20px;
  outline: none;
}
.nav-menu__search {
  background-color: #40b3b7;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-menu__search input {
  padding-left: 8px;
  padding-right: 8px;
  background-color: transparent;
  border: none;
  color: #b3e1e2;
  outline: none;
}
.nav-menu__search input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b3e1e2;
  opacity: 1;
  /* Firefox */
}
.nav-menu__search input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #b3e1e2;
}
.nav-menu__search input::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #b3e1e2;
}
.nav-menu__search button[type="submit"] {
  width: 50px;
  height: 50px;
  background-image: url("../media/images/svg/search-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  background-color: transparent;
}
.nav-menu__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #fff;
}
.nav-menu__links li {
  position: relative;
  border-bottom: 1px solid #ccc;
}
.nav-menu__links li i {
  position: absolute;
  top: 15px;
  left: 16px;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}
.nav-menu__links li a {
  position: relative;
  padding-left: 48px;
  padding-top: 16px;
  padding-bottom: 16px;
  display: block;
}
.nav-menu__links li a::after {
  content: "";
  transition: transform 150ms ease;
  position: absolute;
  top: 20px;
  right: 15px;
  display: block;
  width: 6px;
  height: 11px;
  background-image: url("../media/images/svg/arr-right.svg");
  background-size: 100%;
}
.nav-menu__link-with-nested {
  font-size: 16px;
}
.nav-menu__link-with-nested.is-active ul {
  display: block;
}
.nav-menu__link-with-nested.is-active a::after {
  transform: rotate(90deg);
}
.nav-menu__link-with-nested ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding-left: 20px;
  display: none;
}
.nav-menu__link-with-nested ul li {
  font-size: 14px;
}
.nav-menu__link-with-nested ul li:last-of-type {
  border-bottom: none;
}
.nav-menu__link-with-nested ul li:first-of-type {
  border-top: 1px solid #ccc;
}
.nav-menu__link-with-nested ul li a {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 32px;
}
.nav-menu__link-with-nested ul li a::after {
  display: none;
}
.nav-menu__feedback a {
  display: block;
  padding: 16px 16px 16px 48px;
  background-color: #ffa601;
  color: #fff;
  font-weight: bold;
}
.diseases-links {
  columns: 3;
  column-gap: 20px;
  padding-bottom: 60px;
}
@media (max-width: 1080px) {
  .diseases-links {
    columns: 2;
  }
}
@media (max-width: 760px) {
  .diseases-links {
    columns: 1;
    margin-top: -15px;
  }
}
.diseases-links__item-cont {
  padding-bottom: 10px;
  padding-top: 10px;
}
.diseases-links__item {
  padding: 20px;
  padding-top: 42px;
  break-inside: avoid-column;
  position: relative;
  border: solid 1px #e5e5e5;
  transition: border-color 300ms ease;
}
.diseases-links__item:hover {
  border-color: #8cd1d4;
}
.diseases-links__item:hover h3,
.diseases-links__item:hover::before {
  color: #8cd1d4;
}
.diseases-links__item::before {
  transition: color 300ms ease;
  content: attr(data-letter);
  position: absolute;
  top: 20px;
  left: 24px;
  font-size: 40px;
  line-height: 1;
  text-transform: uppercase;
  font-weight: 700;
  color: #d6d6d6;
}
@media (max-width: 760px) {
  .diseases-links__item::before {
    font-size: 30px;
    top: 16px;
    left: 16px;
  }
}
.diseases-links__item h3 {
  transition: color 300ms ease;
  color: #d6d6d6;
  font-size: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 20px;
  margin-top: -20px;
}
.diseases-links__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding-left: 65px;
}
@media (max-width: 760px) {
  .diseases-links__list {
    padding-left: 30px;
  }
}
.diseases-links__list_padding-off {
  padding: 0;
}
.diseases-links__list li:not(:last-of-type) {
  margin-bottom: 12px;
}
.diseases-links__list li a {
  font-size: 18px;
  text-decoration: underline;
}
@media (max-width: 760px) {
  .diseases-links__list li a {
    font-size: 14px;
  }
}
.diseases-links__list li a:hover {
  text-decoration: none;
}
.hamburger-btn {
  width: 50px;
  height: 55px;
  background-image: url("../media/images/svg/burger-btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 35px;
  border: none;
  background-color: transparent;
  outline: none;
}
.btn {
  padding-top: 18px;
  padding-bottom: 18px;
  color: #ffffff;
  font-weight: bold;
  background-color: #40b3b7;
  border: none;
  font-size: 18px;
  padding-left: 32px;
  padding-right: 32px;
  cursor: pointer;
  width: auto;
}
.btn_bordered {
  border-radius: 3px;
}
.btn_wide {
  width: 100%;
}
.btn_secondary {
  background-color: #ffa601;
}
.btn-close {
  width: 42px;
  height: 42px;
  border: none;
  background: transparent;
  background-image: url("../media/images/svg/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  cursor: pointer;
  outline: none;
}
.scroll-top-btn {
  display: none;
  width: 48px;
  height: 48px;
  border: none;
  background-color: transparent;
  padding: 0;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  background-image: url("../media/images/svg/scroll-top.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.footer {
  padding: 15px 20px 25px;
  border-top: 2px solid #e9e9e9;
}
.footer__links {
  text-align: center;
  margin-bottom: 16px;
}
.footer__links a {
  display: inline-block;
  margin: 0 14px;
  line-height: 30px;
}
.footer__feedback-link {
  color: #ffa601;
}
.footer__copyright {
  font-size: 12px;
  line-height: 18px;
  color: #949494;
  text-align: center;
}
.breadcrumbs ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 1.2;
  margin-top: 16px;
  margin-bottom: 32px;
