  color: #5d5c5c;
  position: relative;
  margin-top: 2px;
  margin-bottom: 4px;
}
.breadcrumbs ul li:not(:first-of-type) {
  padding-left: 22px;
}
.breadcrumbs ul li:not(:first-of-type)::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  left: 10px;
  top: 6px;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 4px solid #7f7f7f;
}
.breadcrumbs ul li a {
  position: relative;
}
.breadcrumbs ul li a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #b0b0b0;
  bottom: 0;
  left: 0;
}
